import $config  from '../config';
import $encode  from './encryptionService';
import $storage from './storageService';

// =================================================
//  Scribbliator Service
//  Application specific details.
//     Local Storage Key    => createKey( data )
//     Local Storage Record => createData( data )
//
function createKey( data ) {
    const { username } = data;
    const { appName, version } = $config;
    return `${appName}-${version}-${username}`;
}

function createRecord( data ) {
    const { email, username, password } = data;
    const date = new Date( );
    const yy = date.getFullYear( );
    const dd = date.getDate( );
    const mm = date.getMonth( ) + 1;

    if( _checkDuplicateUsername( username ).length >= 1 ) {
        return new Error( "Duplicate Username" );
    }

    return {
        username       : username,
        password       : $encode.EncryptBasic( password ),
        email          : email,
        'date-created' : `${yy}-${mm}-${dd}`
    };
}

function _checkDuplicateUsername( username ) {
    if( username === "" ) { return null; }
    
    const keys = $storage.getAllKeys( "Scribbliator" );

    return keys.filter( key => {
        return key.match( username );
    });
}

function _checkDuplicateEmail( email ) {
    if( email === "" ) { return null; }

    const keys = $storage.getAllKeys( "Scribbliator" );

    return keys.filter( key => {
        const _email = JSON.parse( $storage.get( key ) ).email;
        return _email === email;
    });
}


function validateDuplicateUsername( data ) {
    const { username } = data;
    const userList  = _checkDuplicateUsername( username );

    const customError = {
        message : `Duplicate username ${username} found.`,
        path : ["username"],
        type : "username duplicate",
        context: { value : "", invalids: [""], key: "username", label: "username" }
    }

    if( !userList || userList.length === 0 ) { 
        return;
    } else {
        return customError;
    }
}

function validateDuplicateEmail( data ) {
    const { email } = data;
    const userList = _checkDuplicateEmail( email );

    const customError = {
        message : `Duplicate email ${email} found.`,
        path : ["email"],
        type : "email duplicate",
        context: { value : "", invalids: [""], key: "email", label: "email" }
    }

    if( !userList || userList.length === 0 ) { 
        return;
    } else {
        return customError;
    }
}

export default {
    createKey,
    createRecord,
    validateDuplicateUsername,
    validateDuplicateEmail
};